<template>
    <div class="contaner">
        <el-form class="form" ref="form" :model="form">
            <h6 class="title">登录</h6>
            <el-form-item>
                <el-input v-model="form.account" placeholder="请输入账号" autofocus>
                    <i slot="prefix" class="aticonfont el-input__icon aticon-mobile"></i>
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-input v-model="form.passwd" placeholder="请输入密码" type="password" show-password>
                    <i slot="prefix" class="aticonfont el-input__icon aticon-lock"></i>
                </el-input>
            </el-form-item>
            <el-button type="primary" block @click="login">登 录</el-button>
        </el-form>
        <div class="footer">本系统仅提供学习支持，若使用者存在违法行为与本系统无关！</div>
    </div>
</template>

<script>
export default {
    name: "Login",
    metaInfo: {
        title: '登录系统',
    },
    data: function () {
        return {
            form: {
                account: '',
                passwd: ''
            }
        };
    },
    methods: {
        login: function () {
            this.$store.dispatch('login', this.form).then(res=>{
                if (res.status > 0) {
                    this.$message({
                        message: '登录成功！',
                        type: 'success'
                    });
                    this.$router.replace('/bd');
                }else{
                    this.$message.error('请重新检查账号或密码是否正确');
                }
            });
        }
    }
}
</script>

